@charset "UTF-8";
/**
 * jQuery Button Audio Player
 * 1つのボタンで音声を再生できるようにするプラグイン
 * デザインパターンは2種類
 *
 * @package jQuery
 * @since 1.0
 */
.bap-btn {
  appearance: none;
  cursor: pointer;
  user-select: none;
  display: inline-block;
  width: 48px;
  height: 48px;
  margin: 0;
  padding: 0;
  overflow: hidden;
  vertical-align: middle;
  outline: 0;
  border: 0;
  border-radius: 100px;
  transition: 235ms ease; }
  @media (min-width: 768px) {
    .bap-btn {
      width: 48px;
      height: 48px; } }

.bap-btn-default {
  color: #fff;
  text-align: center;
  background-color: #000; }
  .bap-btn-default:hover {
    opacity: 0.8; }
  .bap-btn-default .bap-icon {
    display: block;
    width: 24px;
    height: 20px;
    margin: 0 auto 4px;
    padding: 0 1px;
    line-height: 1; }
    .bap-btn-default .bap-icon svg {
      display: block;
      width: auto;
      height: 15px;
      margin: 0 auto;
      fill: currentColor; }
      @media (min-width: 768px) {
        .bap-btn-default .bap-icon svg {
          height: 20px; } }
  .bap-btn-default .bap-icon-on,
  .bap-btn-default .bap-icon-off {
    display: block; }
  .bap-btn-default .bap-onoff-text {
    line-height: 1;
    font-size: 10px;
    text-indent: 2px;
    letter-spacing: 2px; }

.bap-btn-bar-animation {
  color: #fff;
  background-color: #000; }
  .bap-btn-bar-animation .bap-bars {
    position: relative;
    width: 28px;
    height: 50%;
    margin-left: auto;
    margin-right: auto; }
  .bap-btn-bar-animation .bap-bar {
    position: absolute;
    bottom: 0;
    display: block;
    width: 3px;
    background: currentColor;
    transition: 230ms ease;
    animation-play-state: running; }
    .bap-btn-bar-animation .bap-bar[data-bap-bar="1"] {
      left: 0;
      height: 20%;
      animation: bapSoundBar 2.8s ease .2s infinite alternate; }
    .bap-btn-bar-animation .bap-bar[data-bap-bar="2"] {
      left: 6px;
      height: 45%;
      animation: bapSoundBar 2.2s ease .1s infinite alternate; }
    .bap-btn-bar-animation .bap-bar[data-bap-bar="3"] {
      left: 12px;
      height: 8%;
      animation: bapSoundBar 2.6s ease .1s infinite alternate; }
    .bap-btn-bar-animation .bap-bar[data-bap-bar="4"] {
      left: 18px;
      height: 68%;
      animation: bapSoundBar 2.7s ease .4s infinite alternate; }
    .bap-btn-bar-animation .bap-bar[data-bap-bar="5"] {
      left: 24px;
      height: 48%;
      animation: bapSoundBar 2.4s ease .1s infinite alternate; }
  .bap-btn-bar-animation.bap-deactive .bap-bar {
    animation-play-state: paused; }
  .bap-btn-bar-animation.bap-active .bap-bar {
    animation-play-state: running; }

@keyframes bapSoundBar {
  0% {
    height: 20%; }
  10% {
    height: 50%; }
  20% {
    height: 20%; }
  30% {
    height: 70%; }
  40% {
    height: 10%; }
  50% {
    height: 60%; }
  60% {
    height: 40%; }
  70% {
    height: 100%; }
  80% {
    height: 20%; }
  90% {
    height: 50%; }
  100% {
    height: 100%; } }
